import React from 'react'
import ReactTable from '../../../Helpers/Pagination'
import Button from '@material-ui/core/Button'
import Modal from '@material-ui/core/Modal'
import classes from './HospitalList.module.scss'
import { medicalHospitalListCol } from '../../../../../../../Constant/tableColumnData.json'
import ProfileService from '../../../../../../../Services/Api/Partner/ProfileService'

const HospitalList = ({ open, setOpen }) => {
  const [tableData, setTableData] = React.useState([])
  const [tableColumn, setTableCol] = React.useState(medicalHospitalListCol)
  const [loading, setLoading] = React.useState(true)

  React.useEffect(() => {
    let mounted = true

    if (mounted) {
      fetchData()
    }

    return () => (mounted = false)
  }, [open])

  const fetchData = async () => {
    setLoading(true)
    try {
      let listOfAffiliatedDoctors = []
      const modifiedAffiliatedDoctors = []
      const { data: appData } = await ProfileService.getClinicByType('HOSPITAL')
      setLoading(false)
      const { data: affiliatedDoctor } = appData
      for (let key in affiliatedDoctor) {
        listOfAffiliatedDoctors.push(affiliatedDoctor[key])
      }
      for (let i of listOfAffiliatedDoctors) {
        const obj = {}
        obj.Status = (i || {}).status || ''
        obj.facilityName = (i || {}).name || ''
        obj.medicalPartnerId = (i || {}).id || ''
        obj.location = ((i || {}).address || {}).city || ''
        modifiedAffiliatedDoctors.push(obj)
      }

      setTableData(modifiedAffiliatedDoctors)
      setTableCol(medicalHospitalListCol || [])
    } catch (error) {
      console.log('error', error)
    }
  }

  const HandleCloseModal = data => {
    setOpen(false)
  }

  const memoizedData = React.useMemo(() => tableData, [tableData])
  const memoizedColumns = React.useMemo(() => tableColumn, [tableColumn])

  return (
    <React.Fragment>
      <Modal
        open={open}
        onClose={() => {
          setOpen(false)
        }}
        aria-labelledby='simple-modal-title'
        aria-describedby='simple-modal-description'
      >
        <div id='root' className={classes.root}>
          <div id='container' className={classes.container}>
            <div>
              <ReactTable
                columns={memoizedColumns}
                data={memoizedData}
                CloseModal={HandleCloseModal}
                countName={'Hospital list'}
                link={'/HospitalProfile/PartnerInformation'}
                type={'clinic'}
                loading={loading}
              />
            </div>
            <div id='Close-button'>
              <Button
                className={classes.closeButton}
                variant='contained'
                onClick={() => {
                  setOpen(false)
                }}
              >
                Close
              </Button>
            </div>
          </div>
        </div>
      </Modal>
    </React.Fragment>
  )
}

export default HospitalList
