import React from 'react'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'

const IndexComponent = props => {
  const {openDialog, setOpenDialog, HandleClick} = props

  console.log('openDialog', openDialog)

  return (
    <React.Fragment>
      <Dialog
        id='a_sos_custom_dialog'
        open={openDialog}
      >
        <DialogContent>
          <div id='a_sos_custom_dialog_container' >
            <h3 id='a_sos_custom_dialog_message'>Are you sure you want to delete Procedure?</h3>
            <div id='a_sos_custom_dialog_btn_group' style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
              <Button
                onClick={() => setOpenDialog(false)}
                id='a_sos_custom_dialog_btn_no'
                variant='contained'
                style={{ backgroundColor: '#fd5451', color: 'white', marginRight: '0.5em', marginBottom: '10px' }}
              >
                No
              </Button>
              <Button
                onClick={HandleClick}
                id='a_sos_custom_dialog_btn_yes'
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '0.5em', marginBottom: '10px' }}
              >
                Yes
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </React.Fragment>
  )
}

export default React.memo(IndexComponent)
