import React, { useEffect, useState } from 'react'
import InputBase from '@material-ui/core/InputBase'
import NativeSelect from '@material-ui/core/NativeSelect'
import MenuItem from '@material-ui/core/MenuItem'
import NumberFormat from 'react-number-format'
import Select from '@material-ui/core/Select'
import DropDown from 'Pages/Protected/Partner/ProfileMaintainer/Components/InputFields/DropDown'
import classes from './InputComponents.module.scss'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import { setOfficeLocations } from '../../Store/Action/SpecialService'
import { useDispatch, useSelector } from 'react-redux'

export const StatusSelectComponent = props => {
  const { id = '', value, onChange } = props
  return (
    <NativeSelect id={id} value={value} onChange={onChange} style={{ paddingLeft: 5 }}>
      <option value={'Active'}>Active</option>
      <option value={'Inactive'}>Inactive</option>
      <option value={'On-Hold'}>On-Hold</option>
    </NativeSelect>
  )
}

export const OfficeLocationSelectComponent = props => {
  const dispatch = useDispatch()
  const { officeLocation: newOfficeLocation } = useSelector(state => state.specialService)
  const { id = '', value, onChange } = props
  const [officeLocations, setOfficeLocation] = useState([...newOfficeLocation])
  useEffect(() => {
    async function getOfficeLoc () {
      let res = await AffiliationEncoderService.getOfficeLocation()
      if (res) {
        const { data: { data } } = res
        let arr = []
        for (let i of data) {
          let obj = {}
          obj.value = i.name
          obj.text = i.name
          arr.push(obj)
        }
        dispatch(setOfficeLocations(arr))
        setOfficeLocation(arr)
      }
    }
    if (newOfficeLocation.length <= 0) getOfficeLoc()
  }, [])

  return (
    <DropDown
      id={id}
      items={officeLocations}
      value={value}
      multiline
      onChange={({ target: { value } }) => onChange(value)}
      noBlankValue
      style={{ color: '#020' }}
    />
  )
}

export const OfficeLocationSelectDropDownComponent = props => {
  const { id = '', value, onChange, officeLocations } = props

  const getUnique = (arr, comp) => {
    // store the comparison  values in array
    const unique = arr.map(e => e[comp])

      // store the indexes of the unique objects
      .map((e, i, final) => final.indexOf(e) === i && i)

      // eliminate the false indexes & return unique objects
      .filter((e) => arr[e]).map(e => arr[e])

    console.log('unice data', unique)

    return unique
  }

  return (
    <Select id={id} value={value} className={classes.inputFields} style={{ width: '120px' }} onChange={({ target: { value } }) => onChange(value)}>
      <MenuItem value=''><label style={{ color: 'grey' }}>None</label></MenuItem>
      {getUnique(officeLocations, 'value').map(({ text, value }) => <MenuItem value={value}>{text}</MenuItem>)}
    </Select>
  )
}

export const TextInputComponent = props => {
  const { id = '', error = '', value, onChange, multiline } = props
  return (
    <React.Fragment>
      <InputBase
        value={value}
        type='text'
        onChange={onChange}
        multiline={multiline}
        className={error && !value ? classes.inputFieldsError : classes.inputFields}
        autoComplete='off'
      />
    </React.Fragment>
  )
}

export const NumberInputComponent = props => {
  const { id = '', error = '', disabled = false, value, onChange, format = null, onValueChange = true } = props
  const HandleChange = (newValue) => {
    return onValueChange ? onChange(newValue.formattedValue) : onChange(newValue.value)
  }

  return (
    <React.Fragment>
      <NumberFormat
        id={id}
        className={error ? classes.inputFieldsError : classes.inputFields}
        disabled={disabled}
        format={format}
        value={value}
        onValueChange={values => HandleChange(values)}
        decimalSeparator={false}
        customInput={InputBase}
        isNumericString
        allowLeadingZeros
        allowNegative={false}
        displayType={'input'}
      />
    </React.Fragment>
  )
}
