const initialData = {
  initialData: [],
  data: [],
  dataToSave: [],
  newData: [],
  isEdit: false,
  page: 1,
  count: 1,
  limit: 10,
  filter: {},
  totalItems: 0,
  isCancel: false,
  isSave: false,
  toSave: false,
  success: false,
  openModal: false,
  addSucces: false,
  customInput: false,
  facilityNames: [],
  sort: {
    isSort: true
  }
}

const MaintenanceTableReducer = (state = initialData, action) => {
  switch (action.type) {
    case 'SET_INITIAL_VALUE':
      return { ...state, initialData: action.payload }

    case 'SET_IS_EDIT':
      return { ...state, isEdit: action.payload }

    case 'SET_DATA':
      return { ...state, data: action.payload }

    case 'SET_PREV_COPY':
      const copyData = [ ...state.initialData ]
      return { ...state, data: copyData }

    case 'SET_UPDATE':

      const nameValue = action.payload.name
      const value = action.payload.value === 'INACTIVE' ? 'Inactive'
        : action.payload.value === 'ACTIVE'
          ? 'Active'
          : action.payload.value
      const index = action.payload.index

      const newStateData = [ ...state.data ]
      newStateData[index][nameValue] = value

      return { ...state, data: [ ...newStateData ] }

    case 'SET_PAGE_DATA':

      return { ...state,
        count: action.payload.count,
        limit: action.payload.limit,
        skip: action.payload.skip,
        page: action.payload.page,
        totalItems: action.payload.totalItems
      }

    case 'SET_SORT':
      if (state.data.length === 0) return {...state}
      let sortedData = []

      const name = action.payload.id
      const newData = [ ...state.data ]

      if (state.sort.isSort === true) {
        sortedData = newData.sort((a, b) => {
          if (a[name] < b[name]) return -1
          if (a[name] > b[name]) return 1
          return 0
        })
      } else {
        sortedData = newData.reverse((a, b) => {
          if (a[name] < b[name]) return -1
          if (a[name] > b[name]) return 1
          return 0
        })
      }

      return { ...state, data: [...sortedData], sort: { isSort: !state.sort.isSort } }

    case 'SET_FILTER':
      const fieldName = action.payload.field
      const fieldValue = action.payload.value
      if (typeof fieldName === 'undefined' || typeof fieldValue === 'undefined') return { ...state, filter: {} }

      const newFilter = { ...state.filter, [fieldName]: fieldValue }

      return { ...state,
        filter: newFilter,
        page: 1 }

    case 'SET_CANCEL':
      return { ...state, isCancel: action.payload }

    case 'SET_SAVE':
      const newDataValue = [ ...state.data ]
      return { ...state, isSave: action.payload, dataToSave: newDataValue }

    case 'SET_TO_SAVE':
      return { ...state, toSave: action.payload }

    case 'SET_SUCCESS':
      return { ...state, success: action.payload }

    case 'SET_OPEN_MODAL':
      return { ...state, openModal: action.payload }

    case 'SET_NEW_DATA':
      const dataToBeAdded = {
        desc: action.payload.desc,
        remarks: action.payload.remarks,
        code: action.payload.code,
        status: action.payload.status,
        _id: action.payload._id
      }

      return { ...state, newData: [ dataToBeAdded ] }

    case 'SET_ADD_SUCCESS':
      return { ...state, addSucces: action.payload }

    case 'SET_OPEN_CUSTOM_INPUT':
      return { ...state, customInput: action.payload }

    case 'SET_FACILITY_NAMES':
      return { ...state, facilityNames: action.payload }

    case 'SET_UPDATE_FACILITY_NAMES':

      const facilityNameValue = action.payload.facilityNames
      const uniqueId = action.payload._id
      const indexOfData = Number(action.payload.index)

      const newDataforFacilityNames = [...state.data]
      newDataforFacilityNames[indexOfData]['facilityNames'] = [ ...facilityNameValue ]

      return { ...state, data: [ ...newDataforFacilityNames ] }

    default:
      return { ...state }
  }
}

export default MaintenanceTableReducer
