import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'

export default function CustomAlertSnackbar(props) {
  const { isSave, setIsSave, setDelay } = props

  const delayValue = setDelay

  React.useEffect(() => {
    const autoClose = setTimeout(
      () => {
        if (isSave === true) {
          setIsSave(false)
        }
        /** Todo : Temporaryly commented due to error */
      },
      setDelay ? delayValue : 1500
    )
    return () => clearTimeout(autoClose)
  })

  return (
    <React.Fragment>
      <Dialog open={isSave}>
        <DialogContent>
          <p style={{ padding: '20px', color: '#151c1b' }}>Successfully Saved</p>
        </DialogContent>
      </Dialog>
    </React.Fragment>
  )
}
