function BaseUrlFetcher () {}

/**
 * Fetch Base URL for Client (XURPAS) endpoints
 */
BaseUrlFetcher.getClientBaseUrl = function () {
  let baseURL = this.getRawClientBaseUrl()
  baseURL = baseURL.replace('/api/', '/')
  return baseURL
}

/**
 * Fetch the Raw Base URL for Client (XURPAS) endpoints
 * Known use is only for the UM components from UM library
 */
BaseUrlFetcher.getRawClientBaseUrl = function () {
  let baseURL = window.localStorage.CLIENT_URL || `${process.env.REACT_APP_CLIENT_API}/`
  let parts = baseURL.split('://')
  return parts[0] + '://' + parts[1].replace('//', '/')
}

BaseUrlFetcher.getClaimsBaseUrl = function () {
  let baseURL = window.localStorage.CLAIMS_URL
    ? window.localStorage.CLAIMS_URL.replace(/\/$/, '')
    : window.localStorage.PARTNER_URL
    ? window.localStorage.PARTNER_URL.replace(/\/$/, '')
    : `${process.env.REACT_APP_SERVER_API}`
  return baseURL
}

BaseUrlFetcher.getPartnerBaseUrl = function () {
  let baseURL = window.localStorage.PARTNER_URL
    ? window.localStorage.PARTNER_URL.replace(/\/$/, '')
    : `${process.env.REACT_APP_SERVER_API}`
  return baseURL
}

export default BaseUrlFetcher
