import instance from './../instance'
const axios = instance('/p/os')

export default class OtherServices {
  static sendFrom ({data}) {
    return axios({
      url: '/file-upload',
      method: 'POST',
      data
    })
  }

  static createSpecialOthers (data) {
    return axios({
      url: '/partners/other-services',
      method: 'POST',
      data
    })
  }

  static getOtherServiceById (id) {
    return axios({
      url: `/partners/other-services/id/${id}`,
      method: 'GET'
    })
  }
}
