const initialState = {
  hospitalForms: {
    name: '', // required
    bedCapacity: '', // required
    email: '', // required
    years: '', // required
    months: '', // required
    dohNumber: '',
    dohLicenseType: '',
    dohLicenseExp: '',
    philhealthNumber: '',
    philhealthExpiration: '',
    payee: '', // required
    classification: '', // required
    tinStatusValue: '', // required
    tinStatus: '', // required
    vatTaxRate: '',
    processType: '', // required
    unitFlrBldg: '', // required
    bldgName: '',
    street: '', // required
    subdivision: '', // required
    region: '', // required
    district: '', // required
    barangay: '', // required
    city: '', // required
    contacts: [],
    affiliationInfos: [],
    zipCode: '', // required
    cName: '', // required
    cDesignation: '', // required
    cDepartment: '', // required
    cEmail: '', // required
    cNumber: '', // required
    internalRemarks: '', // required,
    externalRemarks: ''
  }
}

export default function affiliationTypeReducer (state = initialState, { type, payload }) {
  switch (type) {
    case 'SET_AFFILIATION_FORM_DATA':
      return {
        ...state,
        hospitalForms: payload
      }

    default:
      return state
  }
}
