const initialState = {
  doctorIds: [],
  facilityNames: [],
  facilityIds: [],
  doctorSpecialization: [],
  officeLocation: []
}

export default function autoSuggestReducer (state = initialState, { type, payload }) {
  switch (type) {
    case 'SET_DOCTORS_IDS_FOR_AUTO_SUGGEST':
      return {
        ...state,
        doctorIds: payload
      }

    case 'SET_FACILITY_NAME_FOR_AUTO_SUGGEST':
      return {
        ...state,
        facilityNames: payload
      }

    case 'SET_FACILITY_IDS_FOR_AUTO_SUGGEST':
      return {
        ...state,
        facilityIds: payload
      }

    case 'SET_DOCTOR_SPECIALIZATION':
      return {
        ...state,
        doctorSpecialization: payload
      }

    default:
      return state
  }
}
