import SpecialServiceValidator from '../../Validation/SpecialServiceValidator'
import {
  SET_ON_SAVE,
  SET_ON_EDIT,
  SET_PREVIOUS_RECORD,
  SET_ON_VALIDATE,
  UPDATE_DATA,
  SET_PAGE_DATA,
  UPDATE_ROW_DATA,
  COPY_DATA_TO_PREV,
  RESET_STATE,
  SET_TABLE_SIZE,
  SET_SORT_COLUMN_ID,
  SET_SORT_SERVICE_NAME,
  SET_SORT_ADDRESS,
  SET_INPUT_VALUE,
  SET_VALUE,
  UPDATE_DATA_ON_SAVE
} from '../type'
import { updateData } from '../Action/SpecialService'

const initialState = {
  data: [],
  size: 10,
  totalPage: 1,
  page: 1,
  pageData: [],
  prevPageData: [],
  medicalPartnerID: {
    list: [],
    inputValue: '',
    value: '',
    isSort: false
  },
  serviceName: {
    list: [],
    inputValue: '',
    value: '',
    isSort: false
  },
  address: {
    list: [],
    inputValue: '',
    value: '',
    isSort: false
  },
  isEdit: false,
  isRowsValid: false,
  isSaving: false,
  dataToSend: [],
  officeLocation: [],
  locationFilterValue: '',
  officeLocationList: []
}

const stateReducer = (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_DATA:
      return { ...state, data: [...action.payload] }

    case SET_PAGE_DATA:
      let filterValue = {}
      if (action.payload.name !== undefined && action.payload.value !== undefined) {
        filterValue = {
          [action.payload.name]: { ...state[action.payload.name], value: action.payload.value }
        }
      }
      const newState = { ...state, ...filterValue }
      const objectHeaders = {
        id: (newState.medicalPartnerID.value || {}).id || '',
        serviceName: (newState.serviceName.value || {}).serviceName || '',
        address: (newState.address.value || {}).address || '',
        status: '',
        contactPerson: '',
        contactNumber: '',
        tin: '',
        officeLocation: newState.locationFilterValue.value || '',
        error: ''
      }
      const temp = [...newState.data]
      const filteredData = temp.filter(obj => {
        const validation = Object.keys(obj).map(keys => {
          if (objectHeaders[keys] !== '') {
            if (obj[keys] === objectHeaders[keys]) {
              return true
            }
            return false
          }
          return true
        })
        return validation.every(Boolean)
      })

      const size = action.payload.size || state.size
      const totalPage = Math.ceil(filteredData.length / size)
      let page = action.payload.page || state.page
      if (page > totalPage) {
        page = totalPage
      }
      let pageData = filteredData.slice(page * size - size, page * size)
      const prevPageData = [...pageData]
      const medicalPartnerIDList = newState.data.map(obj => ({ id: obj.id }))
      const addressList = newState.data.map(obj => ({ address: obj.address }))
      const serviceNameList = newState.data.map(obj => ({ serviceName: obj.serviceName }))
      const officeLocationList = newState.data.map(obj => ({ value: obj.officeLocation, text: obj.officeLocation }))
      pageData = pageData.sort((a, b) => {
        return a.id.substring(3) - b.id.substring(3)
      })
      return {
        ...newState,
        ...{
          pageData,
          prevPageData,
          size,
          page,
          totalPage,
          medicalPartnerID: { ...newState.medicalPartnerID, list: medicalPartnerIDList },
          serviceName: { ...newState.serviceName, list: serviceNameList },
          address: { ...newState.address, list: addressList },
          officeLocationList
        }
      }

    case UPDATE_ROW_DATA:
      let newData = [...state.pageData]
      const position = action.payload.pos
      const name = action.payload.name
      const newValue = action.payload.value

      newData[position] = { ...state.pageData[position], [name]: newValue }

      return { ...state, pageData: newData }

    case SET_ON_SAVE:
      let isErrorEncountered = false
      const validatedObject = state.pageData.map((obj, index) => {
        let testObj = { ...obj }
        delete testObj['error']

        const error = SpecialServiceValidator(testObj).error


        if (error) isErrorEncountered = true
        return { ...obj, error: error || {} }
      })
      let dataToSend = []

      if (!isErrorEncountered) {
        dataToSend = validatedObject.filter((obj, index) => {
          if (
            obj.status !== state.prevPageData[index].status ||
            obj.address !== state.prevPageData[index].address ||
            obj.serviceName !== state.prevPageData[index].serviceName ||
            obj.contactPerson !== state.prevPageData[index].contactPerson ||
            obj.tin !== state.prevPageData[index].tin ||
            obj.contactNumber !== state.prevPageData[index].contactNumber.toString() ||
            obj.officeLocation !== state.prevPageData[index].officeLocation
          ) {
            return true
          }
          return false
        })
      }
      return {
        ...state,
        ...{ isRowsValid: !isErrorEncountered, pageData: [...validatedObject], isSaving: action.payload, dataToSend }
      }

    case UPDATE_DATA_ON_SAVE:
      const index = state.data.findIndex(obj => obj.id === action.payload.id)
      let dataList = [...state.data]
      dataList[index] = action.payload
      return { ...state, ...{ data: [...dataList] }, locationFilterValue: '' }

    case SET_ON_EDIT:
      return { ...state, isEdit: action.payload }
    case SET_ON_VALIDATE:
      return { ...state }
    case SET_PREVIOUS_RECORD:
      return { ...state, pageData: [...state.prevPageData] }

    case COPY_DATA_TO_PREV:
      return { ...state, prevPageData: [...state.pageData] }

    case SET_SORT_COLUMN_ID:
      let sortedPartnerID = []
      if (state.medicalPartnerID.isSort === true) {
        sortedPartnerID = state.pageData.sort((a, b) => {
          return a.id.substring(3) - b.id.substring(3)
        })
      } else {
        sortedPartnerID = state.pageData.reverse((a, b) => {
          return a.id.substring(3) - b.id.substring(3)
        })
      }
      return {
        ...state,
        pageData: [...sortedPartnerID],
        medicalPartnerID: { ...state.medicalPartnerID, isSort: !state.medicalPartnerID.isSort }
      }

    case SET_SORT_SERVICE_NAME:
      let sortedServiceName = []
      if (state.serviceName.isSort === true) {
        sortedServiceName = state.pageData.sort((a, b) => {
          if (a.serviceName < b.serviceName) return -1
          if (a.serviceName > b.serviceName) return 1
          return 0
        })
      } else {
        sortedServiceName = state.pageData.reverse((a, b) => {
          if (a.serviceName < b.serviceName) return -1
          if (a.serviceName > b.serviceName) return 1
          return 0
        })
      }
      return {
        ...state,
        pageData: [...sortedServiceName],
        serviceName: { ...state.serviceName, isSort: !state.serviceName.isSort }
      }

    case SET_SORT_ADDRESS:
      let sortedAddress = []
      if (state.address.isSort === true) {
        sortedAddress = state.pageData.sort((a, b) => {
          if (a.address < b.address) return -1
          if (a.address > b.address) return 1
          return 0
        })
      } else {
        sortedAddress = state.pageData.reverse((a, b) => {
          if (a.address < b.address) return -1
          if (a.address > b.address) return 1
          return 0
        })
      }
      return {
        ...state,
        pageData: [...sortedAddress],
        address: { ...state.address, isSort: !state.address.isSort }
      }

    case SET_INPUT_VALUE:
      const newInputFilterValue = {
        [action.payload.name]: { ...state[action.payload.name], inputValue: action.payload.inputValue }
      }
      return { ...state, ...newInputFilterValue }

    case 'SET_OFFICE_LOCATION':
      return {
        ...state,
        officeLocation: action.payload
      }
    case RESET_STATE:
      const initState = {...initialState}
      initState.officeLocation = state.officeLocation
      return { ...state, ...initState }
    default:
      return { ...state }
  }
}

export default stateReducer
