import React from 'react'
import TableList from '../../../Components/TableList'
import ProfileService from 'Services/Api/Partner/ProfileService'
import { useHistory } from 'react-router-dom'

const FacilityList = props => {
  const { type, open, setOpen, url = '', paginationText = '' } = props
  const history = useHistory()
  return (
    <TableList
      getAsyncFunc={(data, token) => {
        const args = { ...data, type: type }
        return ProfileService.getDoctor(args, token)
      }}
      readOnlyHeaderTableProps={[
        {
          text: 'Status',
          enableSort: false,
          field: 'status'
        },
        {
          text: 'Medical Partner ID',
          type: 'text',
          field: 'id'
        },
        {
          text: 'Doctor Name',
          type: 'text',
          field: 'name'
        },
        {
          text: 'Specialization',
          type: 'text',
          field: 'specialization'
        },
        {
          text: 'Facility Name',
          type: 'text',
          field: 'facilityName'
        },
        {
          text: 'Clinic Schedule',
          type: 'text',
          field: 'clinicSchedule'
        }
      ]}
      readOnlyFooterButtonsProps={[
        {
          text: 'Close',
          style: { backgroundColor: '#fd5451' },
          onClick: () => setOpen(false)
        }
      ]}
      dataStructure={{
        status: { type: 'text' },
        id: { type: 'text' },
        name: { type: 'text' },
        specialization: { type: 'text' },
        facilityName: { type: 'text' },
        clinicSchedule: { type: 'text' }
      }}
      filter={{
        id: '',
        name: '',
        specialization: '',
        facilityName: '',
        clinicSchedule: ''
      }}
      sort={{
        id: '',
        name: '',
        specialization: '',
        facilityName: '',
        clinicSchedule: ''
      }}
      doubleClick={{
        enable: true,
        onDoubleClick: obj => {
          setOpen(false)
          history.push(`${url}/${obj.id}`)
        }
      }}
      circularProgressWidth={6}
      openModal={open}
      setOpenModal={setOpen}
      paginationText={paginationText}
      type={type}
    />
  )
}

export default FacilityList
