const requiredValidator = (arrayOfObject, checkFields) => {
  if (arrayOfObject.length === 0 || checkFields.length === 0) {
    return { data: [], valid: true }
  }
  let valid = true
  const data = arrayOfObject.map(obj => {
    if ('deleted' in obj) return { deleted: true }
    const fieldObj = {}

    for (let field of checkFields) {
      if (!obj[field]) {
        fieldObj[field] = false
      } else {
        fieldObj[field] = true
      }
    }
    return fieldObj
  })

  for (let obj of data) {
    const isValidObject = Object.keys(obj).some(field => obj[field] === false)

    if (isValidObject) {
      valid = false
      break
    }
  }
  return { data, valid }
}

export default requiredValidator
