const extractData = (baseData, newData) => {
  if (!baseData.length) return newData
  if (baseData.length === 1 && newData.length === 1) return newData

  const data = newData.filter(obj => {
    const result = baseData.find(innerObj => {
      let match = false
      if (obj['_id'] === innerObj['_id']) {
        for (const key in obj) {
          if (obj[key] === innerObj[key]) {
            match = true
          } else {
            match = false
            break
          }
        }
      }
      console.log('matchsss', match)

      return match
    })

    if (result === undefined) return true
    return false
  })

  return data
}

export default extractData
