import React from 'react'
import Grid from '@material-ui/core/Grid'
import Avatar from '@material-ui/core/Avatar'
import { useHistory } from 'react-router-dom'
import IconButton from '@material-ui/core/IconButton'
import NotificationsIcon from '@material-ui/icons/Notifications'
import ArrowDropDown from '@material-ui/icons/ArrowDropDown'

// Styles
import styles from './HeaderComponent.module.scss'
import images from 'Constant/images/images'

// Xurpas
// import { PopperPlacementType } from '@material-ui/core'
// import { Components as XurpasComponents } from '@hims_client/hims-core'

import { Components as XurpasComponents, Utils as XurpasUtils } from '@hims_client/hims-core'

import ConfigController2 from 'Controllers/ConfigController2'

export default function HeaderComponent ({ logout }) {
  const [anchorEl, setAnchorEl] = React.useState(null)

  const [openUmMenu, setOpenUmMenu] = React.useState(false)
  const [placementUmMenu, setPlacementUmMenu] = React.useState()

  const [xurpasData, setXurpasData] = React.useState({})
  const [xurpasAvatar, setXurpasAvatar] = React.useState()

  const wrapperRefXurpas = React.useRef(null)
  const history = useHistory()

  React.useEffect(() => {
    initializeXurpasUmComponents()
  }, [])
  // Note: In case XURPAS UM Components have been fixed to contain the UM data reading functionality,
  //       these code lines will be unecessary. These lines had to be done on Claims Module
  //       because XURPAS is unable to immediately fix the UM Component limitation
  const initializeXurpasUmComponents = async () => {
    // Note: XURPAS Utility is still raw and unrefined so hence AWB utility is used instead
    //       XURPAS utility returns an array instead of an object with keys.
    // const data = await XurpasUtils.StorageService('user_data')
    //
    const data = await ConfigController2.getUserManagementData()

    if (data.profile_pic) {
      setXurpasAvatar('data:image/jpeg;base64,' + data.profile_pic)
    }

    setXurpasData(data || {})
  }
  const handleShowMenu = (event) => {
    const newPlacement = 'bottom-ned'
    setOpenUmMenu(prev => placementUmMenu !== newPlacement || !prev)
    setPlacementUmMenu(newPlacement)
  }
  const redirectToProfile = () => {
    setOpenUmMenu(false)
    setAnchorEl(null)
    history.push('/profile')
  }

  return (
    <Grid item container className={styles.root}>
      <Grid item className={styles['left-container']}>
        <img src={images.logo} alt='logo' />
      </Grid>
      <Grid item className={styles['right-container']}>
        <IconButton>
          <NotificationsIcon />
        </IconButton>

        <Avatar alt='avatar' src={xurpasAvatar} className={styles.avatar} />
        <IconButton
          aria-controls='simple-menu'
          aria-haspopup='true'
          onClick={event => { setAnchorEl(event.currentTarget); handleShowMenu(); event.preventDefault() }}
        >
          <ArrowDropDown />
        </IconButton>

        {/* <Menu
          id='header-menu'
          anchorEl={anchorEl}xurpasData.profile_pic
          open={Boolean(anchorEl)}
          keepMounted
          onClose={() => setAnchorEl(null)}
        >
          <MenuItem id='logout' onClick={handleClick('bottom-end')}>
            Logout
          </MenuItem>
        </Menu> */}
        <div>
          <XurpasComponents.UI.HeaderPopper
            open={openUmMenu}
            anchorEl={anchorEl}
            placement={placementUmMenu}
            wrapperRef={wrapperRefXurpas}
            currentModule='Partner Network'
            modules={xurpasData.modules}
            fullname={xurpasData.full_name}
            profilePic={xurpasData.profile_pic}
            username={xurpasData.username}
            onClose={() => setOpenUmMenu(false)}
            onViewProfileClick={redirectToProfile}
            onLogout={() => logout()}
          />
        </div>
      </Grid>
    </Grid>
  )
}
