const initialState = {
  affiliationType: {
    stageName: '',
    id: ''
  },
  dentistAffiliationType: {
    affiliationStatus: ''
  }
}

export default function affiliationTypeReducer (state = initialState, {type, payload}) {
  switch (type) {
    case 'SET_AFFILIATION_TYPE':

      return {
        ...state,
        affiliationType: payload
      }

    case 'SET_DENTIST_AFFILIATION':
      return {
        ...state,
        dentistAffiliationType: payload
      }
    default:
      return state
  }
}
