export const arrayObjectNullFilter = (list, field = null) => {
  if (list.length === 0) return []
  return list.filter(obj => obj[field] !== null)
}

export const arrayObjectDistinct = (list, field = null) => {
  if (list.length === 0) return []
  return list.filter((obj, index, arr) => {
    return arr.findIndex(obj2 => obj[field] === obj2[field]) === index
  })
}
