import LogServices from 'Services/Api/Log/LogServices'
import ConfigController2 from 'Controllers/ConfigController2'

export default class LogController {
  //       module: CLAIMS
  //       level: 'debug',
  //       url: window.location.href,
  //       agent: window.navigator.userAgent,
  //       date: new Date().toLocaleString(),
  //       msg: msg

  static async log (data, user_id, access_token) {
    if (process.env.REACT_APP_LOG !== 'TRUE') return

    const {
      module = 'CLAIMS',
      level = 'debug',
      url = window.location.href,
      agent = window.navigator.userAgent,
      date = new Date().toLocaleString(),
      message = '',
      details = {}
    } = data

    // const user_data = await ConfigController2.getUserManagementData()
    // const { access_token, user_id } = user_data

    // delete user_data.access_token
    // delete user_data.user_id

    // Trimmed rbac data
    // delete user_data.rbac
    // user_data.role = user_data.role.map(r => ({ ...r, rbac: [] }))

    await LogServices.storeLog(
      {
        module,
        level,
        url,
        agent,
        date,
        message,
        details
      },
      access_token,
      user_id
    )
  }
}
