// import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'
import React from 'react'
import ReactDOM from 'react-dom'
import Button from '@material-ui/core/Button'
import { SnackbarProvider, useSnackbar } from 'notistack'

import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { ErrorPageService } from 'SharedComponents/ErrorPage/ErrorPage'

import { exclude } from './exclude'

const mountPoint = document.createElement('div')
document.body.appendChild(mountPoint)

const toast = (message, { variant }) => {
  const notistackRef = React.createRef()

  const onClickDismiss = key => () => {
    notistackRef.current.closeSnackbar(key)
  }

  const ShowSnackbar = ({ message }) => {
    const { enqueueSnackbar } = useSnackbar()
    enqueueSnackbar(message, { variant })
    return null
  }

  ReactDOM.render(
    // see https://github.com/iamhosseindhv/notistack#snackbarprovider
    <SnackbarProvider
      style={{ top: '30px' }}
      // preventDuplicate
      autoHideDuration={3000}
      maxSnack={5}
      anchorOrigin={{ horizontal: 'right', vertical: 'top' }}
      ref={notistackRef}
      action={key => <Button onClick={onClickDismiss(key)}>x</Button>}
    >
      <ShowSnackbar message={message} variant={variant} />
    </SnackbarProvider>,
    mountPoint
  )
}

const handler = {
  success: response => response,
  fail: error => {
    const { response, config } = error
    if (!exclude({ headers: config.headers, status: 'all' })) {
      if (response) {
        const { xurpas = false } = response.config.headers
        const { status, data } = response

        if (xurpas) {
          const { error = { message: '' } } = data
          // SnackbarService.set({ open: true, message: error.message, severity: 'error' })
          toast(error.message, {
            variant: 'error'
          })
        } else if (status >= 500 && status < 600) {
          // Internal Server Error
          // SnackbarService.set({ open: true, message: 'Something went wrong!', severity: 'error' })
          toast(`Something went wrong!`, {
            variant: 'error'
          })
        } else if (status === 404) {
          // Resource not found
          if (!exclude({ headers: response.config.headers, status })) {
            ErrorPageService.redirect()
          }
        } else if (status === 412) {
          const message = typeof data.message === 'string' ? data.message : data.message.error.message
          // SnackbarService.set({ open: true, message, severity: 'error' })
          toast(message, {
            variant: 'error'
          })
        } else if (status === 401) {
          ErrorPageService.unauthorized()
        }
      } else {
        // No Internet
        try {
          toast('No internet connection!', {
            variant: 'error'
          })
        } catch (err) {}
      }
    }

    LoadingService.loading(false)

    return Promise.reject(error)
  }
}

export default handler
