const initialState = {
  regions: [],
  cities: [],
  barangays: [],
  provinces: []
}

export default function addressReducer (state = initialState, { type, payload }) {
  switch (type) {
    case 'SET_REGIONS_ENCODER':
      return {
        ...state,
        regions: payload
      }

    case 'SET_BARANGAYS_ENCODER':
      return {
        ...state,
        barangays: payload
      }

    case 'SET_CITY_ENCODER':
      return {
        ...state,
        cities: payload
      }

    case 'SET_PROVINCE_ENCODER':
      return {
        ...state,
        provinces : payload
      }

    case 'SET_RESET_ADDRESS_ENCODER':
      return {
        ...state,
        provinces : [],
        cities: [],
        barangays: []

      }

    default:
      return state
  }
}
