import React from 'react'
import Paper from '@material-ui/core/Paper'
import NumberFormat from 'react-number-format'
import TextField from '@material-ui/core/TextField'
import Autocomplete from '@material-ui/lab/Autocomplete'
import { InputBase } from '@material-ui/core'
import MenuItem from '@material-ui/core/MenuItem'
import Select from '@material-ui/core/Select'
import MomentUtils from '@date-io/moment'
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import useDebounce from '../CustomHooks/useDebounce'
import moment from 'moment'

import classes from './InputComponent.module.scss'

const InputComponent = props => {
  const {
    readOnly = true,
    isHeader = false,
    requiredValid = true,
    requiredMessage = '',
    name = '',
    value = '',
    defaultMenu = '',
    index,
    type = 'text',
    isDelete = false,
    editable = true,
    withPaper = true,
    suggestions = [],
    menu = [],
    onClickDeleteRow = '',
    onChangeRow = '',
    onChangeText = ''
  } = props

  const [suggestionsList, setSuggestionsList] = React.useState([])
  const debounceValue = useDebounce(value, 600)

  React.useEffect(() => {
    if (type === 'suggest' && debounceValue && debounceValue.length > 0) {
      const newSuggestionsList = suggestions.filter(suggestion =>
        suggestion.text.toLowerCase().includes(debounceValue.toLowerCase())
      )
      setSuggestionsList(newSuggestionsList)
    } else {
      setSuggestionsList([])
    }
  }, [debounceValue])

  const withPaperComponent = component => {
    return (
      <React.Fragment>
        <div style={{ display: 'flex', flexDirection: 'column', width: '100%' }}>
          <Paper
            style={
              !requiredValid && (value === null || !value)
                ? { display: 'flex', alignItems: 'center', width: '100%', border: '1px solid red', padding: '0 10px' }
                : { display: 'flex', width: '100%', alignItems: 'center', padding: '0 10px' }
            }
          >
            {component}
          </Paper>
          {!requiredValid && (value === null || !value) && <p style={{ color: 'red', margin: 0 }}>{requiredMessage}</p>}
        </div>
      </React.Fragment>
    )
  }
  const dateValue = date => {
    return date === null ? '' : date.format('MM/DD/YYYY')
  }

  const inputBuilder = type => {
    switch (type) {
      case 'text': {
        if ((readOnly && !isHeader) || !editable) {
          return value
        }
        return (
          <InputBase
            value={value === null || !value ? '' : value}
            style={{ width: '100%' }}
            type='text'
            onChange={e =>
              onChangeRow ? onChangeRow(index, name, e.target.value) : onChangeText(name, e.target.value)
            }
          />
        )
      }
      case 'date': {
        if ((readOnly && !isHeader) || !editable) {
          return value ? moment(value).format('L') : ''
        }
        return (
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              allowKeyboardControl={false}
              format='MM/DD/YYYY'
              clearable
              placeholder='mm/dd/yyyy'
              InputProps={{
                disableUnderline: true,
                readOnly: true,
                style: { paddingLeft: '10px' }
              }}
              value={value === '' ? null : value}
              onChange={date => {
                onChangeRow ? onChangeRow(index, name, dateValue(date)) : onChangeText(name, dateValue(date))
              }}
              KeyboardButtonProps={{
                'aria-label': 'change date'
              }}
              keyboardIcon={<FontAwesomeIcon icon='calendar-day' style={{ fontSize: '1rem' }} />}
            />
          </MuiPickersUtilsProvider>
        )
      }
      case 'price': {
        if ((readOnly && !isHeader) || !editable) {
          return value
        }
        return (
          <NumberFormat
            style={{ width: '100%' }}
            value={value}
            onValueChange={value =>
              onChangeRow
                ? onChangeRow(index, name, value.floatValue || '')
                : onChangeText(name, value.floatValue || '')
            }
            // className={classes.integerComponent}
            fixedDecimalScale
            customInput={InputBase}
            thousandSeparator
            decimalScale={2}
            isNumericString
            allowNegative={false}
            displayType={'input'}
            allowLeadingZeros={false}
          />
        )
      }
      case 'number': {
        if ((readOnly && !isHeader) || !editable) {
          return value
        }
        return (
          <NumberFormat
            style={{ width: '100%' }}
            value={value}
            onValueChange={value =>
              onChangeRow
                ? onChangeRow(index, name, value.floatValue || '')
                : onChangeText(name, value.floatValue || '')
            }
            // className={classes.integerComponent}
            decimalSeparator={false}
            customInput={InputBase}
            isNumericString
            allowNegative={false}
            displayType={'input'}
            allowLeadingZeros={false}
          />
        )
      }
      case 'suggest': {
        if ((readOnly && !isHeader) || !editable) {
          return value
        }
        return (
          <Autocomplete
            style={{ width: '100%' }}
            freeSolo
            value={value}
            options={suggestionsList.length > 0 ? suggestionsList.map(option => option.text) : []}
            onChange={(event, newValue) =>
              onChangeRow ? onChangeRow(index, name, newValue) : onChangeText(name, newValue)
            }
            onInputChange={(event, newValue) => {
              onChangeRow(index, name, newValue)
            }}
            renderInput={params => (
              <TextField
                {...params}
                InputProps={{ ...params.InputProps, disableUnderline: true, style: { width: '100%' } }}
              />
            )}
          />
        )
      }
      case 'select': {
        if ((readOnly && !isHeader) || !editable) {
          return value
        }
        return (
          <Select
            style={{ width: '100%' }}
            value={value || defaultMenu}
            onChange={e =>
              onChangeRow ? onChangeRow(index, name, e.target.value) : onChangeText(name, e.target.value)
            }
            disableUnderline
          >
            {menu.length > 0 &&
              menu.map((obj, index) => {
                return (
                  <MenuItem key={`TableListMenuItem-${index}`} value={obj.value}>
                    {obj.text}
                  </MenuItem>
                )
              })}
          </Select>
        )
      }
      default:
        if ((readOnly && !isHeader) || !editable) {
          return value
        }
        return (
          <InputBase
            style={{ width: '100%' }}
            value={value}
            type={type}
            onChange={e =>
              onChangeRow ? onChangeRow(index, name, e.target.value) : onChangeText(name, e.target.value)
            }
          />
        )
    }
  }

  const builder = (type, readOnly, isHeader, editable) => {
    if (readOnly || !editable) {
      if (isHeader) return withPaperComponent(inputBuilder(type))
      return inputBuilder(type)
    } else {
      return withPaperComponent(inputBuilder(type))
    }
  }

  return (
    <React.Fragment>
      <div style={{ width: '100%', display: 'flex', alignItems: 'center' }}>
        {builder(type, readOnly, isHeader, editable)}
        {isDelete && !readOnly && (
          <FontAwesomeIcon
            className={classes.tableHeaderSortIcon}
            icon='trash'
            onClick={() => onClickDeleteRow(index)}
          />
        )}
      </div>
    </React.Fragment>
  )
}

export default React.memo(InputComponent, (prevProps, nextProps) => {
  try {
    if (
      prevProps.value === nextProps.value &&
      prevProps.name === nextProps.name &&
      prevProps.index === nextProps.index &&
      prevProps.readOnly === nextProps.readOnly &&
      prevProps.requiredValid === nextProps.requiredValid
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})
