import React from 'react'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

// Styles
import classes from './style.module.scss'

export default props => {
  const { logout = () => {} } = props
  const [counter, setCounter] = React.useState()

  React.useEffect(() => {
    let _counter = 10
    setCounter(_counter)
    let interval = setInterval(() => {
      if (_counter) {
        _counter--
        setCounter(_counter)
      } else {
        clearInterval(interval)
        logout()
      }
    }, 1000)

    return () => clearInterval(interval)
  }, [])

  return (
    <div className={classes.root}>
      <Typography variant='h4' className={classes.title}>
        Unauthorized
      </Typography>
      <Typography variant='body1' className={classes.message}>
        Session timeout: page was not accessible
      </Typography>

      <div className={classes['action-messages']}>
        <Typography variant='body2' className={classes.message}>
          You will be redirected to Login in {counter}
        </Typography>
        <Typography variant='body2' className={classes.message}>
          Click the button below to go to that page.
        </Typography>
      </div>

      <Button variant='contained' color='primary' className='button-neutral' onClick={logout}>
        Go to Login
      </Button>
    </div>
  )
}
