import React from 'react'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import { formatTinNumber } from '../../../../Components/Utils/utils'
import DropDown from 'Pages/Protected/Partner/ProfileMaintainer/Components/InputFields/DropDown'
import { TextInputComponent, NumberInputComponent, OfficeLocationSelectComponent } from '../InputComponents/InputComponents'
import classes from './RowComponent.module.scss'

const RowComponent = props => {
  const { pos = 0, rowObj = {}, isEdit = false, onChange, officeLocation } = props

  console.log('rowObj', rowObj.error)

  const StatusItems = [
    {id: '501', text: 'Active', value: 'Active'},
    {id: '502', text: 'Inactive', value: 'Inactive'},
    {id: '503', text: 'On-Hold', value: 'On-Hold'}
  ]

  return (
    <React.Fragment>
      <TableRow className={classes.tableBodyRow}>
        <TableCell align='left'>
          {isEdit ? (
            <DropDown
              id={`${rowObj.id}Status`}
              value={rowObj.status}
              items={StatusItems}
              noBlankValue
              style={{color: '#020'}}
              error={!!rowObj.error.status}
              onChange={e => {
                onChange('status', e.target.value)
              }}
            />
          ) : (
            rowObj.status
          )}
        </TableCell>
        <TableCell align='left'>{rowObj.id}</TableCell>
        <TableCell align='left'>
          {isEdit ? (
            <TextInputComponent
              id={`${rowObj.id}serviceName`}
              value={rowObj.serviceName}
              error={!!rowObj.error.serviceName}
              onChange={e => {
                onChange('serviceName', e.target.value)
              }}
            />
          ) : (
            rowObj.serviceName
          )}
        </TableCell>
        <TableCell align='left'>
          {isEdit ? (
            <TextInputComponent
              id={`${rowObj.id}Address`}
              value={rowObj.address}
              error={!!rowObj.error.address}
              onChange={e => {
                onChange('address', e.target.value)
              }}
            />
          ) : (
            rowObj.address
          )}
        </TableCell>
        <TableCell align='left'>
          {isEdit ? (
            <TextInputComponent
              id={`${rowObj.id}ContactPerson`}
              value={rowObj.contactPerson}
              error={!!rowObj.error.contactPerson}
              onChange={e => {
                onChange('contactPerson', e.target.value)
              }}
            />
          ) : (
            rowObj.contactPerson
          )}
        </TableCell>
        <TableCell align='left'>
          {isEdit ? (
            <NumberInputComponent
              id={`${rowObj.id}ContactNumber`}
              value={rowObj.contactNumber}
              error={!!rowObj.error.contactNumber}
              onChange={value => {
                onChange('contactNumber', value)
              }}
            />
          ) : (
            rowObj.contactNumber
          )}
        </TableCell>
        <TableCell align='left'>
          {isEdit ? (
            <NumberInputComponent
              id={`${rowObj.id}tin`}
              value={rowObj.tin}
              format={'###-###-###-###'}
              error={!!rowObj.error.tin}
              onValueChange={false}
              onChange={value => {
                onChange('tin', value)
              }}
            />
          ) : (
            formatTinNumber(rowObj.tin)
          )}
        </TableCell>
        <TableCell align='left'>
          {isEdit
            ? <OfficeLocationSelectComponent
              value={rowObj.officeLocation}
              id={`${rowObj.id}officeLocation`}
              officeLocation={officeLocation}
              onChange={value => {
                onChange('officeLocation', value)
              }} />
            : rowObj.officeLocation}
        </TableCell>
      </TableRow>
    </React.Fragment>
  )
}

export default React.memo(RowComponent, (prevProps, nextProps) => {
  try {
    if (
      prevProps.isEdit === nextProps.isEdit &&
      prevProps.rowObj.status === nextProps.rowObj.status &&
      prevProps.rowObj.serviceName === nextProps.rowObj.serviceName &&
      prevProps.rowObj.address === nextProps.rowObj.address &&
      prevProps.rowObj.contactPerson === nextProps.rowObj.contactPerson &&
      prevProps.rowObj.contactNumber === nextProps.rowObj.contactNumber &&
      prevProps.rowObj.tin === nextProps.rowObj.tin &&
      prevProps.rowObj.officeLocation === nextProps.rowObj.officeLocation &&
      prevProps.rowObj.error.address === nextProps.rowObj.error.address &&
      prevProps.rowObj.error.tin === nextProps.rowObj.error.tin &&
      prevProps.rowObj.error.contactPerson === nextProps.rowObj.error.contactPerson &&
      prevProps.rowObj.error.contactNumber === nextProps.rowObj.error.contactNumber &&
      prevProps.rowObj.error.serviceName === nextProps.rowObj.error.serviceName
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})
