import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableContainer from '@material-ui/core/TableContainer'
import Button from '@material-ui/core/Button'
import TableHead from '@material-ui/core/TableHead'
import Paper from '@material-ui/core/Paper'
import ProfileService from 'Services/Api/Partner/ProfileService'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import CustomAlertSnackBar from '../../../../Components/CustomSnackBar/CustomAlertSnackbar'
import CancelDialog from '../../../Components/CancelDialog/CancelDialog'
import SubmitDialog from '../../../Components/SubmitDialog/SubmitDialog'
import CircularProgress from '@material-ui/core/CircularProgress'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import TextField from '@material-ui/core/TextField'
import FormControl from '@material-ui/core/FormControl'
import Pagination from '@material-ui/lab/Pagination'
import Autocomplete from '@material-ui/lab/Autocomplete'
import MenuItem from '@material-ui/core/MenuItem'
import Select from '@material-ui/core/Select'

import Modal from '@material-ui/core/Modal'
import {
  updateData,
  setPageData,
  setOnEdit,
  updateRowData,
  setPreviousRecord,
  setOnSave,
  setResetState,
  copyDataToPrev,
  setBodySize,
  setSortColumnID,
  setSortServiceName,
  setSortAddress,
  setInputValue,
  setValue,
  updateDataOnSave
} from '../../Store/Action/SpecialService'
import RowComponent from '../../Components/RowComponent/RowComponent'

import classes from './SpecialOtherServices.module.scss'
import { OfficeLocationSelectDropDownComponent } from '../../Components/InputComponents/InputComponents'

const SpecialOtherServices = props => {
  const { open, setOpen } = props
  const dispatch = useDispatch()
  const specialServiceState = useSelector(state => state.specialService)
  const [loaded, setIsLoaded] = React.useState(false)
  const [isCancelDialog, setIsCancelDialog] = React.useState(false)
  const [isSubmitDialog, setIsSubmitDialog] = React.useState(false)
  const [isSaveDialog, setIsSaveDialog] = React.useState(false)

  const fetchData = async () => {
    LoadingService.loading(true)
    const { data: appData } = await ProfileService.getSpecialOtherServices()

    if (appData.data) {
      const newData = appData.data.map(obj => {
        return {
          status: obj.status,
          id: obj.id,
          serviceName: obj.name,
          address: obj.full_address,
          contactPerson: obj.contact.contact_person,
          contactNumber: obj.contact.contact_number,
          tin: obj.tin_number,
          officeLocation: obj.office_location,
          error: {}
        }
      })
      dispatch(updateData(newData))
      dispatch(
        setPageData({
          page: 1,
          size: 10
        })
      )
    }
    LoadingService.loading(false)
  }
  React.useEffect(() => {
    setIsLoaded(true)
    fetchData()
    return () => {
      setOpen(false)
    }
  }, [])

  React.useEffect(() => {
    if (specialServiceState.isSaving && specialServiceState.isRowsValid) {
      setIsSubmitDialog(true)
    }
  }, [specialServiceState.isSaving, specialServiceState.isRowsValid])

  const handleOnSubmit = () => {
    setIsSubmitDialog(false)
    const updateServices = async (newObj, oldObj) => {
      LoadingService.loading(true)
      const { data: appData } = await ProfileService.updateSpecialOtherServices(newObj)
      if (appData.data) {
        dispatch(updateDataOnSave(oldObj))
        dispatch(
          setPageData({
            page: specialServiceState.page,
            size: specialServiceState.size
          })
        )
        dispatch(copyDataToPrev())
        setIsSaveDialog(true)
        dispatch(setOnEdit(false))
      }
      LoadingService.loading(false)
    }
    if (specialServiceState.dataToSend.length > 0) {
      specialServiceState.dataToSend.forEach(obj => {
        const newObj = {
          id: obj.id,
          status: obj.status,
          name: obj.serviceName,
          fullAddress: obj.address,
          contactPerson: obj.contactPerson,
          contactNumber: obj.contactNumber,
          tinNumber: obj.tin,
          officeLocation: obj.officeLocation
        }
        updateServices(newObj, obj)
      })
    } else {
      setIsSaveDialog(true)
      dispatch(setOnEdit(false))
    }
    dispatch(setOnSave(false))
  }

  return (
    <React.Fragment>
      <Modal style={{ outline: 'none' }} open={open} onClose={() => setOpen(false)}>
        <div id='root ' className={classes.root} style={{ outline: 'none' }}>
          <div id='container' className={classes.container}>
            <div className={classes.tableContainer}>
              <div id='Update-button' className={classes.updateButtonDiv}>
                <Button
                  className={classes.updateButton}
                  onClick={() => {
                    dispatch(setOnEdit(true))
                  }}
                  variant='contained'
                >
                  Update
                </Button>
              </div>
              <TableContainer style={{ minHeight: 440, maxHeight: 440, backgroundColor: '#f5f7fb' }}>
                <CustomAlertSnackBar isSave={isSaveDialog} setIsSave={val => setIsSaveDialog(false)} />
                <CancelDialog
                  isCancel={isCancelDialog}
                  setIsCancel={setIsCancelDialog}
                  handleOnCancel={() => {
                    dispatch(setOnEdit(false))
                    dispatch(setPreviousRecord())
                    setIsCancelDialog(false)
                  }}
                />
                <SubmitDialog
                  isSubmit={isSubmitDialog}
                  setIsSubmit={() => {
                    setIsSubmitDialog(false)
                    dispatch(setOnSave(false))
                  }}
                  handleOnSubmit={handleOnSubmit}
                />
                <Table className={classes.table} style={{width: '100%', overflowX: 'scroll'}}>
                  <TableHead className={classes.tableHeader}>
                    <TableRow>
                      <TableCell style={{ width: '8%', verticalAlign: 'top' }} className={classes.tableHeaderCell}>
                        <span className={classes.tableHeaderTitle}>Status</span>
                      </TableCell>
                      <TableCell style={{ width: '16%', whiteSpace: 'nowrap' }} className={classes.tableHeaderCell}>
                        <div>
                          <span className={classes.tableHeaderTitle}>Medical Partner ID</span>
                          <FontAwesomeIcon
                            icon='sort'
                            className={classes.tableHeaderSortIcon}
                            onClick={() => {
                              dispatch(setSortColumnID())
                            }}
                          />
                        </div>
                        <Paper>
                          <Autocomplete
                            id='specialOtherServicesMedicalPartnerID'
                            options={specialServiceState.medicalPartnerID.list}
                            getOptionLabel={option => option.id}
                            onInputChange={(event, newInputValue) => {
                              dispatch(
                                setInputValue({
                                  name: 'medicalPartnerID',
                                  inputValue: newInputValue
                                })
                              )
                            }}
                            inputValue={specialServiceState.medicalPartnerID.inputValue}
                            value={specialServiceState.medicalPartnerID.value}
                            onChange={(event, newValue) => {
                              dispatch(
                                setPageData({
                                  name: 'medicalPartnerID',
                                  value: newValue || '',
                                  page: 1,
                                  size: specialServiceState.size
                                })
                              )
                            }}
                            renderInput={params => (
                              <TextField
                                {...params}
                                InputProps={{
                                  ...params.InputProps,
                                  disableUnderline: true,
                                  style: { paddingLeft: '10px' }
                                }}
                              />
                            )}
                          />
                        </Paper>
                      </TableCell>
                      <TableCell style={{ width: '14%' }} className={classes.tableHeaderCell}>
                        <div>
                          <span className={classes.tableHeaderTitle}>Service Name</span>
                          <FontAwesomeIcon
                            icon='sort'
                            className={classes.tableHeaderSortIcon}
                            onClick={() => {
                              dispatch(setSortServiceName())
                            }}
                          />
                        </div>
                        <Paper>
                          <Autocomplete
                            id='specialOtherServicesServiceName'
                            options={specialServiceState.serviceName.list}
                            getOptionLabel={option => option.serviceName}
                            onInputChange={(event, newInputValue) => {
                              dispatch(
                                setInputValue({
                                  name: 'serviceName',
                                  inputValue: newInputValue
                                })
                              )
                            }}
                            inputValue={specialServiceState.serviceName.inputValue}
                            value={specialServiceState.serviceName.value}
                            onChange={(event, newValue) => {
                              dispatch(
                                setPageData({
                                  name: 'serviceName',
                                  value: newValue || '',
                                  page: 1,
                                  size: specialServiceState.size
                                })
                              )
                            }}
                            renderInput={params => (
                              <TextField
                                {...params}
                                InputProps={{
                                  ...params.InputProps,
                                  disableUnderline: true,
                                  style: { paddingLeft: '10px' }
                                }}
                              />
                            )}
                          />
                        </Paper>
                      </TableCell>
                      <TableCell style={{ width: '22%' }} className={classes.tableHeaderCell}>
                        <div>
                          <span className={classes.tableHeaderTitle}>Full Address</span>
                          <FontAwesomeIcon
                            icon='sort'
                            className={classes.tableHeaderSortIcon}
                            onClick={() => {
                              dispatch(setSortAddress())
                            }}
                          />
                        </div>
                        <Paper>
                          <Autocomplete
                            id='specialOtherServicesFullAddress'
                            options={specialServiceState.address.list}
                            getOptionLabel={option => option.address}
                            onInputChange={(event, newInputValue) => {
                              dispatch(
                                setInputValue({
                                  name: 'address',
                                  inputValue: newInputValue
                                })
                              )
                            }}
                            inputValue={specialServiceState.address.inputValue}
                            value={specialServiceState.address.value}
                            onChange={(event, newValue) => {
                              dispatch(
                                setPageData({
                                  name: 'address',
                                  value: newValue || '',
                                  page: 1,
                                  size: specialServiceState.size
                                })
                              )
                            }}
                            renderInput={params => (
                              <TextField
                                {...params}
                                InputProps={{
                                  ...params.InputProps,
                                  disableUnderline: true,
                                  multiline: true,
                                  style: { paddingLeft: '10px' }
                                }}
                              />
                            )}
                          />
                        </Paper>
                      </TableCell>
                      <TableCell style={{ width: '13%', verticalAlign: 'top' }} className={classes.tableHeaderCell}>
                        <span className={classes.tableHeaderTitle}>Contact Person</span>
                      </TableCell>
                      <TableCell style={{ width: '13%', verticalAlign: 'top' }} className={classes.tableHeaderCell}>
                        <span className={classes.tableHeaderTitle}>Contact Number</span>
                      </TableCell>
                      <TableCell style={{ width: '12%', verticalAlign: 'top' }} className={classes.tableHeaderCell}>
                        <span className={classes.tableHeaderTitle}>TIN</span>
                      </TableCell>
                      <TableCell style={{ width: '16%', verticalAlign: 'top' }} className={classes.tableHeaderCell}>
                        <span className={classes.tableHeaderTitle}>Office Location</span>
                        <OfficeLocationSelectDropDownComponent
                          officeLocations={specialServiceState.officeLocationList}
                          onChange={(newValue) => {
                            dispatch(
                              setPageData({
                                name: 'locationFilterValue',
                                value: newValue || '',
                                page: 1,
                                size: specialServiceState.size
                              })
                            )
                          }}
                        />
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody className={classes.tableBody}>
                    {!loaded && (
                      <div className={classes.loadingComponent}>
                        <CircularProgress />
                      </div>
                    )}
                    {loaded && (
                      <React.Fragment>
                        {specialServiceState.pageData.map((dataObj, index) => {
                          return (
                            <RowComponent
                              key={`${dataObj.id}_${index}`}
                              rowObj={dataObj}
                              pos={index}
                              isEdit={specialServiceState.isEdit}
                              onChange={(name, value) =>
                                dispatch(
                                  updateRowData({
                                    name: name,
                                    pos: index,
                                    value: value
                                  })
                                )
                              }
                            />
                          )
                        })}
                      </React.Fragment>
                    )}
                  </TableBody>
                </Table>
              </TableContainer>
              <div className={classes.tablePagination}>
                <div>
                  <span>
                    {specialServiceState.pageData.length}-{specialServiceState.size} out of{' '}
                    {specialServiceState.data.length} Special Services Partners
                  </span>
                </div>
                <div className={classes.options}>
                  <span style={{ marginRight: '10px' }}>
                    <p>Show</p>
                  </span>
                  <FormControl size='small' variant='outlined'>
                    <Select
                      style={{ backgroundColor: 'white' }}
                      value={specialServiceState.size}
                      onChange={e => dispatch(setPageData({ page: 1, size: e.target.value }))}
                    >
                      <MenuItem value={10}>10</MenuItem>
                      <MenuItem value={15}>15</MenuItem>
                      <MenuItem value={20}>20</MenuItem>
                    </Select>
                  </FormControl>
                  <Pagination
                    count={specialServiceState.totalPage}
                    page={specialServiceState.page}
                    onChange={(e, value) => dispatch(setPageData({ page: value, size: specialServiceState.size }))}
                    showFirstButton
                    showLastButton
                  />
                </div>
              </div>
            </div>
            <div className={classes.buttonsGroup}>
              {specialServiceState.isEdit ? (
                <React.Fragment>
                  <Button
                    onClick={() => {
                      setIsCancelDialog(true)
                    }}
                    id='rc_btn_cancel'
                    className={classes.btnRed}
                    variant='contained'
                  >
                    Cancel
                  </Button>
                  <Button
                    onClick={() => dispatch(setOnSave(true))}
                    id='rc_btn_cancel'
                    className={classes.btnGreen}
                    variant='contained'
                  >
                    Save
                  </Button>
                </React.Fragment>
              ) : (
                <div className={classes.footer}>
                  <Button
                    onClick={() => {
                      dispatch(setResetState())
                      setOpen(false)
                    }}
                    id='rc_btn_cancel'
                    className={classes.btnRed}
                    variant='contained'
                  >
                    Close
                  </Button>
                </div>
              )}
            </div>
          </div>
        </div>
      </Modal>
    </React.Fragment>
  )
}

export default SpecialOtherServices
