import React from 'react'

const useModifiedObjectHook = obj => {
  const [prevObj, setPrevObj] = React.useState({})
  const [modified, setModified] = React.useState(false)

  React.useEffect(() => {
    setPrevObj({ ...obj })
  }, [])

  React.useEffect(() => {
    if (!(Object.keys(obj).length === 0 && Object.keys(prevObj).length)) {
      const modified = compareObject(obj, prevObj)
      setModified(!modified)
      if (!modified) {
        setPrevObj(obj)
      }
    }
  }, [obj])

  const compareArray = (first, second) => {
    if (first.length !== second.length) return false
    let isMatch = true
    for (const index in first) {
      if (!isMatch) return false
      if (typeof first[index] === 'object' && typeof second[index] === 'object') {
        isMatch = compareObject(first[index], second[index])
      } else if (Array.isArray(first[index]) && Array.isArray(second[index])) {
        isMatch = compareArray(first[index], second[index])
      } else {
        if (!compareType(first[index], second[index])) return false
        isMatch = first[index] === second[index]
      }
    }
    return isMatch
  }

  const compareObject = (obj, prevObj) => {
    if (Object.keys(prevObj).length !== Object.keys(obj).length) return true
    if (!Object.keys(prevObj).length && !Object.keys(obj).length) return false
    let isMatch = true
    for (const key in obj) {
      if (!isMatch) return false
      if (!(key in prevObj)) return false
      if (!compareType(obj[key], prevObj[key])) return false
      if (Array.isArray(obj[key]) && Array.isArray(prevObj[key])) {
        isMatch = compareArray(obj[key], prevObj[key])
      }
      if (typeof obj[key] === 'object' && typeof prevObj[key] === 'object') {
        isMatch = compareObject(obj[key], prevObj[key])
      }
      isMatch = obj[key] === prevObj[key]
    }
    return isMatch
  }

  const compareType = (first, second) => {
    return typeof first === typeof second
  }

  return { prevObj, modified, obj }
}

export default useModifiedObjectHook
