import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import Paper from '@material-ui/core/Paper'
import TextField from '@material-ui/core/TextField'
import Autocomplete from '@material-ui/lab/Autocomplete'
import CircularProgress from '@material-ui/core/CircularProgress'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import MomentUtils from '@date-io/moment'
import useDebounce from '../CustomHooks/useDebounce'

import classes from './PartnerHeaderCell.module.scss'

const PartnerHeaderCell = props => {
  // Control Props
  const {
    enableSort = true,
    enableAutoComplete = true,
    field = {},
    loading = false,
    options = [],
    inputValue = '',
    width = {},
    value = null
  } = props
  // Type Props
  const { headerTitle, keyProp = '' } = props
  // Getter Props
  const { onChangeValue = () => {}, onClickSort = () => {}, onChangeInputValue = () => {}, onChangeFilter = {} } = props
  const [isSort, setIsSort] = React.useState(true)

  React.useEffect(() => {
    onClickSort(field, isSort)
  }, [isSort])

  const onChangeAutoCompleteInputValue = (filter, val) => {
    onChangeInputValue(val)
  }
  const onChangeAutoCompleteValue = (filter, val) => {
    onChangeValue(val)
  }

  const onHandleClickSort = () => {
    setIsSort(!isSort)
  }

  return (
    <React.Fragment>
      <TableCell key={`partnerHeaderCell_${keyProp}`} style={width} className={classes.tableHeaderCell}>
        <div className={classes.headerFields}>
          <span className={classes.tableHeaderTitle}>{headerTitle}</span>
          {enableSort && (
            <FontAwesomeIcon icon='sort' className={classes.tableHeaderSortIcon} onClick={onHandleClickSort} />
          )}
        </div>
        {enableAutoComplete && (
          <Paper>
            <Autocomplete
              id={`partnerHeaderCellID_${keyProp}`}
              inputValue={inputValue}
              value={value}
              loading={loading}
              options={options}
              getOptionLabel={option => (option ? option[field] : '')}
              onInputChange={(event, newInputValue) => onChangeAutoCompleteInputValue(field, newInputValue)}
              onChange={(event, newValue) => onChangeAutoCompleteValue(field, newValue)}
              getOptionSelected={(option, value) => option[field] === value[field]}
              renderInput={params => (
                <TextField
                  {...params}
                  InputProps={{
                    ...params.InputProps,
                    disableUnderline: true,
                    style: { paddingLeft: '10px' },
                    endAdornment: (
                      <React.Fragment>
                        {loading ? <CircularProgress color='inherit' size={20} /> : null}
                        {params.InputProps.endAdornment}
                      </React.Fragment>
                    )
                  }}
                />
              )}
            />
          </Paper>
        )}
      </TableCell>
    </React.Fragment>
  )
}

export default PartnerHeaderCell
