import axios from 'axios'
import GenerateFormData from 'Utilities/GenerateFormData'
import ErrorHandler from 'Services/ErrorHandler'
import BaseUrlFetcher from 'Services/BaseUrlFetcher'

const addToken = (data = {}) => !(data.hasOwnProperty('isAuthorized') && !data.isAuthorized)

const createInstance = (url = '') => {
  let instance = axios.create()
  let baseURL = null

  try {
    instance.interceptors.request.use(request => {
      // Do something before request is sent
      if (!baseURL) {
        if (url === '/client' || url === '/icd') {
          baseURL = window.localStorage.CLIENT_URL || `${process.env.REACT_APP_CLIENT_API}/`
          baseURL = baseURL.replace('/api/', '/')
        } else {
          console.log('PARTNER URL TEST', BaseUrlFetcher.getPartnerBaseUrl())
          baseURL = `${BaseUrlFetcher.getPartnerBaseUrl()}${url}`
          console.log('BASEURL', baseURL)
        }
      }

      request.baseURL = baseURL

      const { data } = request

      if (addToken(data)) request.headers['Authorization'] = `Bearer ${window.localStorage.getItem('token')}`

      if (data) delete data.isAuthorized

      if (data && !request.headers['skip_generate_form_data']) request.data = GenerateFormData(data)

      // Check if login is made via hims-app and not awb-only app
      // if (window.localStorage.getItem('hims_login') === 'true') {
      //   request.headers['X-Hims-App'] = 'hims-app'
      // }

      request.headers['user_location'] = window.localStorage.getItem('user_location') || 'Head Office'

      return request
    })

    instance.interceptors.response.use(ErrorHandler.success, ErrorHandler.fail)
  } catch (e) {
    console.error('entering localStorage catch block')
    console.error(e)
    console.error('leaving localStorage catch block')
  } finally {
    // comment out
    // console.log('entering and leaving the localStorage finally block')
  }
  return instance
}

export default createInstance
