const initialState = {
  medClinicForms: {
    name: '', // required
    email: '', // required
    years: '', // required
    months: '', // required
    dohNumber: null,
    dohLicenseType: '',
    dohLicenseExp: '',
    philhealthNumber: '',
    philhealthExpiration: '',
    payee: '', // required
    classification: '', // required
    nonVatable: '', // required
    tinStatus: '', // required
    vatTaxRate: null,
    processType: '', // required
    unitFlrBldg: '', // required
    bldgName: '',
    street: '', // required
    subdivision: '', // required
    region: '', // required
    district: '', // required
    barangay: '', // required
    city: '', // required
    contacts: [],
    affiliationInfos: [],
    zipCode: '', // required
    clinicSchedule: '', // required
    roomNumber: '', // required
    internalRemarks: '', // required,
    externalRemarks: ''
  }
}

export default function affiliationTypeReducer (state = initialState, { type, payload }) {
  switch (type) {
    case 'SET_MED_CLINIC_FORM_DATA':
      let newPayload = { ...payload }
      return {
        ...state,
        medClinicForms: newPayload
      }

    default:
      return state
  }
}
