const initialState = {
  facilityDiscountToDelete: []
}

export default function facilityDiscountToDelete (state = initialState, { type, payload }) {
  switch (type) {
    case 'SET_FACILITY_TO_BE_DELETED':
      return {
        ...state,
        facilityDiscountToDelete: payload
      }

    default:
      return state
  }
}
