    civilStatus: '',
    gender: '',
    contactNumber: '',
    majorSpecialization: '',
    otherSpecialization: '',
    ptrNumber: '',
    s2Number: '',
    prcExpiration: '',
    philhealthNumber: '',
    philhealthExpiration: '',
    payee: '',
    tinStatus: '',
    nonVatable: '',
    vatTaxRate: '',
    facilityName: '',
