import NotFoundPage from './404/404'
import UnauthorizedPage from './401/401'

import { useHistory } from 'react-router-dom'

let history = null

const ErrorPageInit = props => {
  history = useHistory()
  return ''
}

const ErrorPageService = {
  redirect: () => {
    history.push('/404')
  },
  unauthorized: () => {
    history.push('/401')
  }
}

export { ErrorPageInit, ErrorPageService, NotFoundPage, UnauthorizedPage }
