// import Axios from 'axios'
import instance from '../instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'

const axios = instance('/p/f')
const axiosPd = instance('/p/d')
const axiosPos = instance('/p/os')
const axiosCom = instance('p/c')
