import React from 'react'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import InputComponent from '../../../InputComponent/InputComponent'
import classes from './TableRow.module.scss'

const TableRowsComponent = props => {
  const {
    readOnly,
    readOnlyHeaderTableProps,
    onUpdateHeaderTableProps,
    rowData = [],
    dataStructure = {},
    requiredData = [],
    onClickDeleteRow,
    onChangeRow,
    doubleClick
  } = props

  const generateTableCells = (
    obj,
    requiredObj,
    dataStructure,
    index,
    onChangeRow,
    readOnly,
    readOnlyHeaderTableProps,
    onUpdateHeaderTableProps
  ) => {
    if (readOnly) {
      return readOnlyHeaderTableProps.map(key => {
        return (
          <TableCell>
            <InputComponent
              key={`tableListCell-${index}`}
              requiredValid={!(key.field in requiredObj) || requiredObj[key.field]}
              readOnly={readOnly}
              index={index}
              style={{ width: '100%' }}
              name={key.field}
              value={obj[key.field]}
              {...dataStructure[key.field]}
              onChangeRow={onChangeRow}
            />
          </TableCell>
        )
      })
    }
    return onUpdateHeaderTableProps.map(key => {
      return (
        <TableCell>
          <InputComponent
            key={`tableListCell-${index}`}
            requiredValid={!(key.field in requiredObj) || requiredObj[key.field]}
            readOnly={readOnly}
            index={index}
            style={{ width: '100%' }}
            name={key.field}
            value={obj[key.field]}
            {...dataStructure[key.field]}
            onClickDeleteRow={onClickDeleteRow}
            onChangeRow={onChangeRow}
          />
        </TableCell>
      )
    })
  }

  const generateTableRow = (
    data,
    requiredData,
    dataStructure,
    onChangeRow,
    readOnly,
    readOnlyHeaderTableProps,
    onUpdateHeaderTableProps
  ) => {
    return (
      <React.Fragment>
        {data.map((obj, index) => {
          const requiredObj = requiredData[index] || {}
          if (obj.deleted === true) {
            return <React.Fragment />
          }
          return (
            <TableRow
              key={`tableListRow-${index}`}
              className={doubleClick.enable ? classes.onHover : ''}
              onDoubleClick={doubleClick.enable ? () => doubleClick.onDoubleClick(obj) : () => {}}
            >
              {generateTableCells(
                obj,
                requiredObj,
                dataStructure,
                index,
                onChangeRow,
                readOnly,
                readOnlyHeaderTableProps,
                onUpdateHeaderTableProps
              )}
            </TableRow>
          )
        })}
      </React.Fragment>
    )
  }

  return (
    <React.Fragment>
      {rowData.length > 0 &&
        generateTableRow(
          rowData,
          requiredData,
          dataStructure,
          onChangeRow,
          readOnly,
          readOnlyHeaderTableProps,
          onUpdateHeaderTableProps,
          doubleClick
        )}
    </React.Fragment>
  )
}

export default TableRowsComponent
