const initialState = {
  dentalClinicForm: {
    clinicName: '',
    contactPersonNo: '',
    email: '',
    contactPerson: '',
    payee: '',
    unitFlrBldg: '',
    bldgName: '',
    street: '',
    subdivision: '',
    region: '',
    district: '',
    barangay: '',
    city: '',
    zipCode: '',
    clinicSchedule: '',
    natureOfPractice: '',
    internalRemarks: '',
    externalRemarks: '',
    roomNumber: '',
    dentalAssociates: [],
    affiliationInfos: []
  },
  dentistProfile: {
    firstName: '',
    middleName: '',
    lastName: '',
    suffix: '',
    birthDate: '',
    civilStatus: '',
    gender: '',
    emailAddress: '',
    contactNumber: '',
    mainSpecialization: '',
    majorSpecialization: '',
    otherSpecialization: '',
    subSpecialization: '',
    pda: '',
    prcNumber: '',
    prcExpiration: '',
    philhealthNumber: '',
    philhealthExpiration: '',
    tinStatus: '',
    tinNumber: '',
    vatTaxRate: '',
    nonVatable: ''
  },
  medicalDentistId: '',
  medicalDentistIdError: '',
  dentistPrcNumber: '',
  prcNumberError: ''
}

export default function dentalClinicAffilForm (state = initialState, { type, payload }) {
  switch (type) {
    case 'SET_MEDICAL_DENTIST_ID':
      console.log('medidcal dentist id reducer', payload)
      return {
        ...state,
        medicalDentistId: payload
      }

    case 'SET_DENTAL_CLINIC_AFFIL_FORM':
      return {
        ...state,
        dentalClinicForm: payload
      }

    case 'SET_MEDICAL_DENTIST_PRC_NUMBER':
      return {
        ...state,
        dentistPrcNumber: payload
      }

    case 'SET_MEDICAL_DENTIST_ERROR':
      return {
        ...state,
        medicalDentistIdError: payload
      }

    case 'SET_PRC_NUMBER_ERROR':
      return {
        ...state,
        prcNumberError: payload
      }

    case 'SET_DENTIST_PROFILE':
      console.log('state profile', state.dentistProfile)
      const profile = {...state.dentistProfile, ...payload}
      return {
        ...state,
        dentistProfile: profile

      }

    default:
      return state
  }
}
