import instance from '../instance'
const facilities = instance('/p/f')
export default class FacilityGroupingService {
  static async add (data) {
    return facilities({
      url: `/partners/room_type/add`,
      method: 'POST',
      data
    })
  }

  static async get () {
    return facilities({
      url: `/partners/room_types`,
      method: 'GET'
    })
  }

  static async update (data) {
    // await axios.put(`${URL}/p/f/partners/room_type/${data._id}`, { roomTypeDesc: data.desc, roomTypeRemarks: data.remarks, roomTypeCode: data.code, status: data.status })
    return facilities({
      url: `/partners/room_type/${data._id}`,
      method: 'PUT',
      data: { roomTypeDesc: data.desc, roomTypeRemarks: data.remarks, roomTypeCode: data.code, status: data.status }
    })
  }
  static async AddRoomType (data) {
    return facilities({
      url: `/partners/room_type/addRoomType`,
      method: 'POST',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static async updateRoomType (args) {
    return facilities({
      url: `/partners/room_type/update`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data: {...args}
    })
  }

  static getRoomTypes (argParams, argCancelToken) {
    const params = {
      skip: argParams.offset,
      limit: argParams.limit,
      filter: argParams.filter
    }

    return facilities({
      url: `/partners/get_room_types`,
      method: 'GET',
      headers: { skip_generate_form_data: 1 },
      params,
      cancelToken: argCancelToken.cancelToken
    })
  }
}
