import {
  SORT_MEDICAL_PARTNER,
  SORT_SERVICE_NUMBER,
  SET_ON_SAVE,
  SET_ON_EDIT,
  SET_PREVIOUS_RECORD,
  SET_ON_VALIDATE,
  UPDATE_DATA,
  SET_PAGE_DATA,
  UPDATE_ROW_DATA,
  RESET_STATE,
  COPY_DATA_TO_PREV,
  SET_TABLE_SIZE,
  SET_SORT_COLUMN_ID,
  SET_SORT_SERVICE_NAME,
  SET_SORT_ADDRESS,
  SET_INPUT_VALUE,
  SET_VALUE,
  UPDATE_DATA_ON_SAVE
} from '../type'

export const setOnEdit = payload => {
  return {
    type: SET_ON_EDIT,
    payload
  }
}

export const setOnSave = payload => {
  return {
    type: SET_ON_SAVE,
    payload
  }
}

export const setPreviousRecord = () => {
  return {
    type: SET_PREVIOUS_RECORD
  }
}

export const setOnValidate = payload => {
  return {
    type: SET_ON_VALIDATE,
    payload
  }
}

export const updateData = payload => {
  return {
    type: UPDATE_DATA,
    payload
  }
}

export const setPageData = payload => {
  return {
    type: SET_PAGE_DATA,
    payload
  }
}

export const updateRowData = payload => {
  return {
    type: UPDATE_ROW_DATA,
    payload
  }
}

export const setResetState = () => {
  return {
    type: RESET_STATE
  }
}

export const copyDataToPrev = () => {
  return {
    type: COPY_DATA_TO_PREV
  }
}

export const setBodySize = payload => {
  return {
    type: SET_TABLE_SIZE,
    payload
  }
}

export const setSortColumnID = () => {
  return {
    type: SET_SORT_COLUMN_ID
  }
}

export const setSortServiceName = () => {
  return {
    type: SET_SORT_SERVICE_NAME
  }
}

export const setSortAddress = () => {
  return {
    type: SET_SORT_ADDRESS
  }
}

export const setInputValue = payload => {
  return {
    type: SET_INPUT_VALUE,
    payload
  }
}

export const setValue = payload => {
  return {
    type: SET_VALUE,
    payload
  }
}

export const updateDataOnSave = payload => {
  return {
    type: UPDATE_DATA_ON_SAVE,
    payload
  }
}

export function setOfficeLocations (payload) {
  return {
    type: 'SET_OFFICE_LOCATION',
    payload
  }
}
