import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Table, TableContainer, TableBody, TableHead, TableRow, Modal, Button } from '@material-ui/core'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import TableHeader from './Components/TableHeader/TableHeader'
import TablePagination from './Components/TablePagination/TablePagination'
import TableCell from '@material-ui/core/TableCell'
import CircularProgress from '@material-ui/core/CircularProgress'
import CancelDialog from './Components/CancelDialog/CancelDialog'
import SubmitDialog from './Components/SubmitDialog/SubmitDialog'
import TableRows from './Components/TableRow/TableRow'
import actions from './Redux/Actions/Actions'
import useAsyncHook from '../../CustomHooks/useAsyncHook2'
import useDebounce from '../../CustomHooks/useDebounce'
import UploadRates from '../../ProfileMaintainer/Components/UploadRates/UploadRates'
import CustomAlert from '../CustomSnackBar/CustomAlert'
import CustomDialog from '../CustomDialog/CustomDialog'
import DeleteModal from '../../ProfileMaintainer/Components/DeleteModal'
import DeepClone from 'Utilities/Partner/DeepClone'
import useModifiedObjectHook from '../../CustomHooks/useModifiedObjectHook'

import classes from './TableList.module.scss'

const TableList = props => {
  // Modal Display Controller
  const { openModal, setOpenModal, setHasChanges = null } = props
  const dispatch = useDispatch()
  const state = useSelector(state => state)
  // Header Buttons
  const {
    readOnlyHeadersButtonsProps = [],
    onUpdateHeadersButtonsProps = [],
    readOnlyFooterButtonsProps = [],
    onUpdateFooterButtonsProps = [],
    readOnlyHeaderTableProps = [],
    onUpdateHeaderTableProps = [],
    dataStructure = {},
    saveAsyncFunc = null,
    getAsyncFunc = null,
    isRowDeletable = null,
    filter = {},
    sort = {},
    circularProgressWidth = 5,
    doubleClick = { enable: false, onDoubleClick: () => {} },
    errorList = {},
    paginationText = 'Procedures',
    validation = {},
    tableStyle = {},
    modalStyle = {}
  } = props
  const { paramId, type } = props
  const [dataSave, errorSave, loadingSave, executeAsyncSave] = useAsyncHook(saveAsyncFunc)
  const [dataGet, errorGet, loadingGet, executeAsyncGet] = useAsyncHook(getAsyncFunc)
  const [isLoaded, setIsLoaded] = React.useState(false)
  const [isUpload, setIsUpload] = React.useState(false)
  const [errorDialog, setErrorDialog] = React.useState(false)
  const filterDebounce = useDebounce(state.filter, 800)
  const [openDelete, setOpenDelete] = React.useState(false)
  const [indexToDelete, setIndexTodelete] = React.useState(null)
  const { modified } = useModifiedObjectHook(filterDebounce)

  React.useEffect(() => {
    if (openModal === false) {
      dispatch(actions.setUpload(false))
    }
    dispatch(
      actions.setInitialData({
        dataStructure: dataStructure,
        filter: filter,
        sort: sort,
        validation: validation
      })
    )
    fetchTable()
    return () => {
      LoadingService.loading(false)
    }
  }, [openModal])

  React.useEffect(() => {
    fetchTable(() => {
      if (isLoaded === false) {
        setIsLoaded(true)
      }
    })
  }, [state.page, state.limit, filterDebounce])

  React.useEffect(() => {
    const errorKeys = Object.keys(errorSave)
    if (errorKeys.length && Object.keys(errorList).length) {
      const errorKey = Object.keys(errorList).find(keyField => keyField === errorSave.message)
      if (errorKey) {
        setErrorDialog({
          status: true,
          message: errorList[errorKey]
        })
      }
    }
  }, [errorSave])

  // React.useEffect(() => {
  //   if (Object.keys(filterDebounce).length > 0 && isLoaded) {
  //     console.log('I WAS CALLED')
  //     fetchTable()
  //   }
  // }, [filterDebounce])

  React.useEffect(() => {
    if (isLoaded) {
      if (state.fromUploaded) {
        dispatch(actions.setToReadMode(false))
      }
      fetchTable(() => dispatch(actions.onClickUpload(false)))
    }
  }, [state.fromUploaded])

  React.useEffect(() => {
    if (Object.keys(dataGet).length > 0) {
      let lastPage = 1
      let page = state.page
      if (state.readOnly) {
        lastPage = Math.ceil(dataGet.data.count / dataGet.data.limit)
      } else {
        lastPage =
          state.lastPage > Math.ceil(dataGet.data.count / dataGet.data.limit)
            ? state.lastPage
            : Math.ceil(dataGet.data.count / dataGet.data.limit)

        if (dataGet.data.list.length === dataGet.data.limit && state.isToAddRow) {
          lastPage += 1
          page = state.page + 1
        }
      }

      dispatch(
        actions.setInitialData({
          dataStructure: dataStructure,
          data: DeepClone(dataGet.data.list) || [],
          newData: DeepClone(dataGet.data.list) || [],
          count: dataGet.data.count,
          total: dataGet.data.list.length,
          page: page,
          limit: dataGet.data.limit,
          lastPage: lastPage || 1
        })
      )
    }

    if (state.isToAddRow) {
      dispatch(actions.addData())
    }
  }, [dataGet])

  React.useEffect(() => {
    if (dataSave && dataSave.data && dataSave.data.length > 0) {
      fetchTable(() => {
        dispatch(actions.showSaveAlert(true))
        dispatch(actions.setToReadMode(true))
        if (setHasChanges !== null) setHasChanges(false)
      })
    }
  }, [dataSave])

  React.useEffect(() => {
    if (state.validateSave) {
      saveTable(() => {
        dispatch(actions.validateSave(false))
      })
    }
  }, [state.validateSave])

  const saveTable = async (cb = null) => {
    dispatch(actions.setUpload(false))

    if (state.extractedData.length === 0) {
      dispatch(actions.setToReadMode(true))
      dispatch(actions.showSaveAlert(true))
    } else {
      await executeAsyncSave({ data: state.extractedData, fromUploaded: state.fromUploaded })
    }

    dispatch(actions.setSubmit(false))
    if (setHasChanges !== null) setHasChanges(true)
    if (cb !== null) {
      cb()
    }
  }

  const fetchTable = async (cb = null) => {
    await executeAsyncGet({
      offset: (state.page - 1) * state.limit,
      limit: state.limit,
      fromUploaded: state.fromUploaded,
      filter: state.filter
    })
    if (cb !== null) {
      cb()
    }
  }

  const generateTableButtons = buttonProps => {
    return buttonProps.map((buttonProps, index) => {
      const { text, style, onClick, component = null } = buttonProps
      if (component !== null) {
        return <React.Fragment key={`${text}-${index}`}>{component}</React.Fragment>
      }
      return (
        <Button
          key={`${text}-${index}`}
          style={{
            color: '#f5f7fb',
            textTransform: 'none',
            margin: '0 5px',
            ...style
          }}
          variant='contained'
          onClick={() =>
            onClick({
              readOnly: state.readOnly,
              setReadOnly: val => dispatch(actions.setToReadMode(val)),
              setIsCancel: val => dispatch(actions.setCancel(val)),
              setIsSubmit: val => dispatch(actions.setSubmit(val)),
              onClickAdd: () => addData(state),
              setUploadRates: val => setIsUpload(val),
              getTriggerFunction: async () => {
                await executeAsyncGet({
                  offset: 0,
                  limit: 10,
                  filter: {}
                })
              }
            })
          }
        >
          {text}
        </Button>
      )
    })
  }

  const addData = state => {
    if (state.page === state.lastPage && state.limit > state.newData.length) {
      dispatch(actions.addData())
    } else if (state.page === state.lastPage && state.limit === state.newData.length) {
      dispatch(actions.isToAddRow())
      dispatch(actions.setTable({ page: state.lastPage + 1, lastPage: state.lastPage + 1 }))
    } else {
      dispatch(actions.isToAddRow())
      dispatch(actions.setTable({ page: state.lastPage }))
    }
  }

  const generateTableHeaders = headerTableList => {
    return headerTableList.map((header, index) => {
      return (
        <TableHeader
          key={index}
          {...header}
          sort={state.sort}
          filter={state.filter}
          field={header.field}
          onChangeText={(key, value) => dispatch(actions.updateFilter({ key, value }))}
          setSort={(field, sortVal) => dispatch(actions.setSort({ key: field, value: sortVal }))}
        >
          {header.text}
        </TableHeader>
      )
    })
  }

  const handleOnCancel = () => {
    dispatch(actions.setUpload(false))
    dispatch(actions.setToReadMode(true))
    dispatch(actions.setCancel(false))
    dispatch(actions.resetData())
  }

  const onHandleSave = async () => {
    dispatch(actions.validateSave(true))
  }

  const HandleDeleteRow = indexValue => {
    setOpenDelete(true)
    setIndexTodelete(indexValue)
  }

  const HandleClickDeleteRow = () => {
    dispatch(actions.deleteRow(indexToDelete))
    setOpenDelete(false)
  }

  const onGenerateTableData = (data, dataStructure, requiredData) => {
    return (
      <TableRows
        rowData={[...data]}
        readOnly={state.readOnly}
        readOnlyHeaderTableProps={readOnlyHeaderTableProps}
        onUpdateHeaderTableProps={onUpdateHeaderTableProps}
        dataStructure={dataStructure}
        requiredData={requiredData}
        onClickDeleteRow={index => isRowDeletable ? HandleDeleteRow(index) : dispatch(actions.deleteRow(index))}
        onChangeRow={(index, key, value) => dispatch(actions.updateData({ index, key, value }))}
        doubleClick={doubleClick}
      />
    )
  }

  return (
    <React.Fragment>
      {isLoaded && (
        <Modal style={{ ...modalStyle }} open={openModal} onClose={setOpenModal}>
          <div className={classes.root}>
            <div className={classes.container}>
              <div className={classes.tableContainer}>
                <div className={classes.tableButtonsGroup}>
                  {(readOnlyHeadersButtonsProps.length > 0 || onUpdateHeadersButtonsProps.length > 0) &&
                    generateTableButtons(state.readOnly ? readOnlyHeadersButtonsProps : onUpdateHeadersButtonsProps)}
                </div>
                <DeleteModal openDialog={openDelete} setOpenDialog={setOpenDelete} HandleClick={HandleClickDeleteRow} />
                <TableContainer
                  style={{
                    height: 400,
                    width: '1100px',
                    overflowX: 'scroll',
                    backgroundColor: '#f5f7fb',
                    ...tableStyle
                  }}
                >
                  <UploadRates
                    isUpload={isUpload}
                    setIsUpload={val => setIsUpload(val)}
                    id={paramId}
                    type={type}
                    customFunc={() => {
                      dispatch(actions.setUpload(true))
                      dispatch(actions.onClickUpload(true))
                    }}
                    {...props}
                  />
                  <CustomAlert
                    onActivate={errorDialog.status}
                    customFunc={() =>
                      setErrorDialog({
                        ...errorDialog,
                        status: false
                      })
                    }
                  >
                    {errorDialog.message}
                  </CustomAlert>
                  <CustomAlert
                    onActivate={state.hasDuplicate}
                    customFunc={() => dispatch(actions.setHasDuplicate(false))}
                  >
                    No duplicate row allowed.
                  </CustomAlert>
                  <CustomAlert
                    onActivate={state.showSaveAlert}
                    customFunc={() => dispatch(actions.showSaveAlert(false))}
                  >
                    Successfully Saved
                  </CustomAlert>
                  <CustomAlert
                    onActivate={state.showSavePageAlert}
                    customFunc={() => dispatch(actions.showSaveNextPageAlert(false))}
                    delay={2000}
                  >
                    Save data before transferring to other page.
                  </CustomAlert>

                  <CancelDialog
                    isCancel={state.isCancel}
                    setIsCancel={val => dispatch(actions.setCancel(val))}
                    handleOnCancel={handleOnCancel}
                  />
                  <SubmitDialog
                    isSubmit={state.isSubmit}
                    setIsSubmit={val => dispatch(actions.setSubmit(val))}
                    handleOnSubmit={onHandleSave}
                  />
                  <Table className={classes.table} size='small' aria-label='simple table'>
                    <TableHead style={{ background: '#9ea3ac' }}>
                      <TableRow>
                        {(readOnlyHeaderTableProps.length > 0 || onUpdateHeaderTableProps.length > 0) &&
                          generateTableHeaders(state.readOnly ? readOnlyHeaderTableProps : onUpdateHeaderTableProps)}
                      </TableRow>
                    </TableHead>
                    <TableBody className={classes.tableBody}>
                      {(loadingGet || loadingSave) && (
                        <TableRow>
                          <TableCell colSpan={circularProgressWidth}>
                            <div className={classes.loadingComponent}>
                              <CircularProgress />
                            </div>
                          </TableCell>
                        </TableRow>
                      )}
                      {!(loadingGet || loadingSave) &&
                        onGenerateTableData(state.newData, state.dataStructure, state.requiredData)}
                    </TableBody>
                  </Table>
                </TableContainer>
                <TablePagination
                  page={state.page}
                  total={state.newData.length}
                  pageCount={state.count}
                  lastPage={state.lastPage}
                  limit={state.limit}
                  text={paginationText}
                  onClickLimit={val => dispatch(actions.setTable({ limit: val }))}
                  onClickPage={val => dispatch(actions.setTable({ page: val }))}
                />
              </div>
              <div className={classes.tableFooterButtonsGroup}>
                {(readOnlyFooterButtonsProps.length > 0 || onUpdateFooterButtonsProps.length > 0) &&
                  generateTableButtons(state.readOnly ? readOnlyFooterButtonsProps : onUpdateFooterButtonsProps)}
              </div>
            </div>

          </div>
        </Modal>
      )}
    </React.Fragment>
  )
}

export default TableList
