const initialState = {
  affiliatedContacts: [],
  doctorAffiliatedContacts: [],
  medicalClinicAffiliatedContacts: []
}

export default function affiliationTypeReducer (state = initialState, { type, payload }) {
  switch (type) {
    case 'SET_LIST_AFFILIATION_CONTACTS':
      return {
        ...state,
        affiliatedContacts: payload
      }

    case 'SET_LIST_DOCTOR_CONTACTS':
      return {
        ...state,
        doctorAffiliatedContacts: payload
      }
    case 'SET_LIST_MEDICAL_CLINIC_CONTACTS':
      return {
        ...state,
        medicalClinicAffiliatedContacts: payload
      }
    default:
      return state
  }
}
